.polls_wrapper {
  display: flex;
  justify-content: flex-start;
  min-height: 57vh;
}

.card {
  width: 250px;
  height: 200px;
  color: white;
  margin: 1em;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-decoration: none;
  padding: 0 1.5em;
  text-align: center;
  cursor: pointer;
  position: relative;
  text-shadow: -1px 1px 6px rgba(255, 255, 255, 0.3);
}

.card:hover .icon {
  box-shadow: 0 0 10px #ff4c29;
}

.card p {
  margin: 0.5em 0;
  font-size: 0.8em;
  display: none;
  opacity: 0;
  transition: all 0.4s;
}

.warning {
  margin: 20px 0;
  padding: 0 25px;
  color: rgb(201, 19, 19);
  text-shadow: 0px 2px 13px rgba(207, 0, 0, 0.48);
  font-size: 16px;
  text-align: left;
}

.available_polls {
  display: block;
  text-align: center;
  margin: 0 auto;
  text-shadow: -2px 0px 8px rgba(0, 0, 0, 0.38);
}

.card:hover p {
  display: block;
  opacity: 1;
}
.card h4 {
  margin-top: 0.5em;
}
.card::before,
.card::after {
  content: '';
  position: absolute;
  background: #3f0071;
  width: 100%;
  height: 40%;
  z-index: -10;
  border-radius: 15px 15px 0 0;
}

.card::before {
  top: 0;
}

.card:hover::before {
  transform: translateY(-20px);
}

.card::after {
  bottom: 10px;
  height: 60%;
  border-radius: 0 0 15px 15px;
}

.icon {
  width: 60px;
  height: 60px;
  background: #ff4c29;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.icon i {
  font-size: 1.5em;
}

.main_wrapper {
  width: 90%;
  margin: 20px auto 0;
  padding: 20px;
  box-shadow: 0.2px 0.9px 7.6px rgba(0, 0, 0, 0.171),
    0.5px 2.4px 20.9px rgba(0, 0, 0, 0.245),
    1.2px 5.7px 50.3px rgba(0, 0, 0, 0.319), 4px 19px 167px rgba(0, 0, 0, 0.49);
  border-radius: 15px;
}

@media (max-width: 500px) {
  .polls_wrapper {
    display: block;
    min-height: 60vh;
  }

  .card {
    margin: 10px auto;
  }
}
